<template>
  <div>
    <div class="button-container">
      <span class="grade-text">{{ grade }}학년</span>
      <div class="buttons">
        <button @click="addRecords">추가</button>
        <button @click="deleteRecords">삭제</button>
        <button @click="saveRecords">저장</button>
      </div>
    </div>
      <table>
        <thead>
          <tr>
            <th>이수</th>
            <th>필수</th>
            <th>과목명</th>
            <th>학점</th>
            <th>출석점수</th>
            <th>과제점수</th>
            <th>중간고사</th>
            <th>기말고사</th>
            <th>총점</th>
            <th>평균</th>
            <th>성적</th>
          </tr>
        </thead>
        <tbody>
            <tr v-for="(record, index) in records" :key="index" @click="selectRow(index)" :class="{ 'selected': selectedRows.includes(index) }">
            <td>
              <select v-model="record.type">
                <option value="교양">교양</option>
                <option value="전공">전공</option>
              </select>
            </td>
            <td>
              <select v-model="record.required">
                <option value="필수">필수</option>
                <option value="선택">선택</option>
              </select>
            </td>
            <td><input type="text" v-model="record.name" /></td>
            <td><input type="number" :value="record.credit" @input=" updateField($event, index, 'credit')" /></td>
            <td><input type="number" :value="record.attendance" @input="updateField($event, index, 'attendance')" /></td>
            <td><input type="number" :value="record.assignment" @input="updateField($event, index, 'assignment')"/></td>
            <td><input type="number" :value="record.midterm" @input="updateField($event, index, 'midterm')"/></td>
            <td><input type="number" :value="record.finalterm" @input="updateField($event, index, 'finalterm')"/></td>
            <td>{{ record.total }}</td>
            <td>{{ record.average }}</td>
            <td :class="{ 'rate-f': record.rate === 'F' }">{{ record.rate }}</td>
          </tr>
        </tbody>
        <tfoot>
            <tr>
            <td colspan="3">합계</td>
            <td>{{ totalCredits }}</td>
            <td>{{ totalAttendance }}</td>
            <td>{{ totalAssignments }}</td>
            <td>{{ totalMidterms }}</td>
            <td>{{ totalFinalterms }}</td>
            <td>{{ grandTotal }}</td>
            <td>{{ averageTotal }}</td>
            <td>{{ averageRate }}</td>
          </tr>
        </tfoot>
      </table>
      <br><br><br>
    </div>
  </template>
  
  <script>
    export default {
      props: ['grade'],
      data() {
        return {
          records: Array(1).fill().map(() => ({
            type: '', required: '', name: '', credit: 0, attendance: 0,
            assignment: 0, midterm: 0, finalterm: 0, total: 0, average: '0.00', rate: ''
          })),
          totalCredits: 0,
          totalAttendance: 0,
          totalAssignments: 0,
          totalMidterms: 0,
          totalFinalterms: 0,    
          grandTotal: 0,
          averageTotal: '0.00',
          averageRate: '',
          selectedRows: [],
        };
      },
      methods: {
        selectRow(index) {
          const selectedIndex = this.selectedRows.indexOf(index);
          if (selectedIndex >= 0) {
            this.selectedRows.splice(selectedIndex, 1);
          } else {
            this.selectedRows.push(index);
          }
        },
        deleteRecords() {
          this.selectedRows.sort((a, b) => b - a);
          for (const index of this.selectedRows) {
            this.records.splice(index, 1);
          }
          this.selectedRows = [];
        },
        addRecords() {
          this.records.push({ type: '', required: '', name: '', credit: 0, attendance: 0, assignment: 0, midterm: 0, finalterm: 0 });
        },
        removeRecord() {
          this.records.pop();
        },
        updateField(event, index, field) {
          let value = Number(event.target.value);
          if (field === 'attendance' || field === 'assignment') {
            value = value > 20 ? 20 : value < 0 ? 0 : value;
          } else if (field === 'midterm' || field === 'finalterm') {
            value = value > 30 ? 30 : value < 0 ? 0 : value;
          }

          this.records[index][field] = value;
        },
        saveRecords() {
          // 중복 check
          const subjectNames = new Set(this.records.map(record => record.name.trim()));
          if (subjectNames.size !== this.records.length) {
            alert('동일한 과목이 있습니다. 확인해주세요.');
            return;
          }


          // 계산 영역 계산
          this.records.forEach(record => {
            const total = this.calcTotal(record);
            record.total = total;
            record.average = this.calcAverage(total);
            record.rate = this.calcRate(record);
          });

          this.totalCredits = this.calcTotals('credit');
          this.totalAttendance = this.calcTotals('attendance');
          this.totalAssignments = this.calcTotals('assignment');
          this.totalMidterms = this.calcTotals('midterm');
          this.totalFinalterms = this.calcTotals('finalterm');
          this.grandTotal = this.records.reduce((acc, record) => acc + record.total, 0);
          this.averageTotal = this.records.length > 0 ? (this.grandTotal / this.records.length).toFixed(2) : '0.00';
          this.averageRate = this.calcRate({ total: this.averageTotal });
          this.sortRecords();
        },

        calcTotals(field) {
          return this.records.reduce((acc, record) => acc + Number(record[field]), 0);
        },

        calcTotal(record) {
          const total=parseInt(record.attendance) + parseInt(record.assignment) +
            parseInt(record.midterm) + parseInt(record.finalterm);
            return total > 100 ? 100 : total < 0 ? 0 : total;
        },

        calcAverage(total) {
          return (total / 4).toFixed(2);
        },

        calcRate(record) {
          if (record.credit === 1) {
            return record.total >= 60 ? 'P' : 'N/P';
          }
        
          return  record.total >= 95 ? 'A+' :
                  record.total >= 90 ? 'A0' :
                  record.total >= 85 ? 'B+' :
                  record.total >= 80 ? 'B0' :
                  record.total >= 75 ? 'C+' :
                  record.total >= 70 ? 'C0' :
                  record.total >= 65 ? 'D+' :
                  record.total >= 60 ? 'D0' : 'F';
        },
        
        sortRecords() {
          this.records.sort((a, b) => {
            if (a.type !== b.type) {
              return a.type.localeCompare(b.type);
            }
            if (a.required !== b.required) {
              return a.required.localeCompare(b.required);
            }
            return a.name.localeCompare(b.name);
          });
        },
      },
    };

  </script>
  
  <style>
  
    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }

    table {
      width: 100%;
      border-collapse: collapse;
      background-color: #cbc8c8;
    }
    
    th {
      background-color: rgb(97, 94, 248);
      color: #ffffff;
    }
    
    td {
      border: 1px solid #fefdff;
      text-align: center;
      width : 50px;
      height : 20px;
      color: rgb(0, 0, 0);
    }
    
    .rate-f {
      background-color: #ff0000;
      color: aliceblue;
    }
    
    input[type="number"], input[type="text"], select {
      border: 1px solid #ffffff;
      outline: none;
      text-align: center;
      font-size : 16px;
      width: 100%;
      height: 100%;
      background-color: rgb(172, 209, 237);
      box-sizing: border-box; 
      margin: 0;
      padding: 0; 
    }
    
    input[type="number"]:focus, input[type="text"]:focus, select:focus {
      background-color: #ffffff;
      border-color: rgb(255, 255, 255);
    }

    .button-container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 10px;
    }
    
    .grade-text {
      margin-left: 10px;
      font-size: 25px;
    }
    
    .buttons {
      display: flex;
      gap: 10px;
    }
    
    button {
      background-color: #fefefe;
      border: 1px solid #000000;
      padding: 8px 16px;
      cursor: pointer;
      color: rgb(0, 0, 0);
      margin-top: 10px;
    }

    tfoot tr td {
      text-align: center;
      vertical-align: middle;
    }
    
    .selected {
      background-color: #a0a0a0;
    }
  </style>